import "react-responsive-carousel/lib/styles/carousel.min.css";
import { Carousel } from 'react-responsive-carousel';
import img from '../js/imagenes'
import '../css/hobbies.css'

const Hobbies = () => {
    return (
        <article className="hobbies">
            <div className="titulo">
                <h3>About me</h3>
            </div>
            <Carousel className="carrucel" interval={3000} autoPlay={true} width="50%" infiniteLoop={true}>
                <div className="divcarrucel">
                    <img src={img.isaac} alt="videojuego" />
                    <p className="legend">I like to play videogames, one of my favourites is the binding of isaac.</p>
                </div>
                <div className="divcarrucel">
                    <img src={img.web} alt="web" />
                    <p className="legend">I'm interested in web development, I have worked with react, node, express,
                    mysql and socketIO.</p>
                </div>
                <div className="divcarrucel">
                    <img src={img.chihayafuru} alt="anime" />
                    <p className="legend">I like to watch anime and read manga.</p>
                </div>
            </Carousel>
        </article>
    )
}

export default Hobbies