import '../css/cryptography.css'
import img from '../js/imagenes'

const Cryptography = () => {
    return (
        <div>
            <div className="cryptography">
                <h3>About cryptography</h3>
            </div>
            <main className="cards">
                <article className="card">
                    <img src={img.turing} alt="Sample" />
                    <div className="text">
                        <h3>Alan Turing</h3>
                        <p>Alan Mathison Turing OBE FRS (23 June 1912 ‚Äì 7 June 1954) was an English 
                            mathematician, computer scientist, logician, cryptanalyst, philosopher, and theoretical 
                            biologist. Turing was highly influential in the development of theoretical computer 
                            science, providing a formalisation of the concepts of algorithm and computation with the 
                            Turing machine, which can be considered a model of a general-purpose computer.
                            Turing is widely considered to be the father of theoretical computer science and artificial 
                            intelligence. </p>
                    </div>
                </article>
                <article className="card">
                    <img src={img.des} alt="Sample" />
                    <div className="text">
                        <h3>DES</h3>
                        <p>DES was the result of a research project set up by International Business Machines (IBM) 
                            corporation in the late 1960‚Äôs which resulted in a cipher known as LUCIFER. In the early 
                            1970‚Äôs it was decided to commercialise LUCIFER and a number of significant changes were 
                            introduced. IBM was not the only one involved in these changes as they sought technical 
                            advice from the National Security Agency (NSA) (other outside consultants were involved 
                            but it is likely that the NSA were the major contributors from a technical point of view). 
                            The altered version of LUCIFER was put forward as a proposal for the new national encryption 
                            standard requested by the National Bureau of Standards (NBS). It was finally adopted in 1977 
                            as the Data Encryption Standard - DES (FIPS PUB 46).</p>
                    </div>
                </article>
                <article className="card">
                    <img src={img.nsa} alt="Sample" />
                    <div className="text">
                        <h3>NSA</h3>
                        <p>The National Security Agency (NSA) is a national-level intelligence agency of the United 
                            States Department of Defense, under the authority of the Director of National Intelligence. 
                            The NSA is responsible for global monitoring, collection, and processing of information and 
                            data for foreign and domestic intelligence and counterintelligence purposes, specializing in a 
                            discipline known as signals intelligence (SIGINT). The NSA is also tasked with the protection of 
                            U.S. communications networks and information systems. The NSA relies on a variety of measures
                             to accomplish its mission, the majority of which are clandestine.</p>
                    </div>
                </article>
            </main>
        </div>
    )
}

export default Cryptography